import React, { useState, useEffect } from 'react';
import { FaTwitter } from "react-icons/fa";
import { FaQuoteLeft } from "react-icons/fa";
import './index.css';

const url = 'https://gist.githubusercontent.com/camperbot/5a022b72e96c4c9585c32bf6a75f62d9/raw/e3c6895ce42069f0ee7e991229064f167fe8ccdc/quotes.json';


function App() {
  const colorArr = ['#16a085',
    '#27ae60',
    '#2c3e50',
    '#f39c12',
    '#e74c3c',
    '#9b59b6',
    '#FB6964',
    '#342224',
    '#472E32',
    '#BDBB99',
    '#77B1A9',
    '#73A857'];
  const randomcolor = Math.floor((Math.random() * colorArr.length));


  const [data, setData] = useState(null);
  const [text, setText] = useState("");
  const [author, setAuthor] = useState("");
  const [loading, setLoading] = useState(true);
  const [color, setColor] = useState({});
  const [bgcolor, setbgColor] = useState({});



  const fetchData = async () => {
    try {
      const res = await fetch(url);
      const obj = await res.json();
      const arr = obj.quotes;

      setData(arr);
      setLoading(false)
      setText(arr[rannum].quote);
      setAuthor(arr[rannum].author);
      setColor({ color: colorArr[randomcolor] })
      setbgColor({ backgroundColor: colorArr[randomcolor] })
    } catch (error) {
      console.log(error)
    }

  }
  useEffect(() => {
    fetchData()
  }, [])

  const rannum = Math.floor(Math.random() * 102);




  const handleClick = () => {
    setText(data[rannum].quote);
    setAuthor(data[rannum].author);
    setColor({ color: colorArr[randomcolor] });
    setbgColor({ backgroundColor: colorArr[randomcolor] });

  }
console.log(data)

  return (
    <div className="conatiner" style={bgcolor}>
      <div id="quote-box">
        <p id="text" style={color}><FaQuoteLeft className="quote-logo" />{loading ? "Loading..." : text} </p>
        <p id="author" style={color}>- {author} </p>
        <div className="btn-conatiner">
          <a id="tweet-quote" href="https://twitter.com/home" className="btn" title="Tweet this quote!"
            target="_top"><FaTwitter style={color} /></a>
          <button id="new-quote" className="btn" style={bgcolor} onClick={handleClick} >New quote</button>
        </div>
      </div>
      <h4>by shiva</h4>
    </div>
  );
}

export default App;
